import React from 'react';
import 'moment/locale/id'
import moment from 'moment';
import { Modal } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import { downloadDataMateri } from '../redux/actions/myactions';
import { variabelConstant } from './constant';


const Content = () => {
	const dataArsip = useSelector(state => state.semuaReducers.detailArsip.itemdetail);
	// let waktu = moment('20180320').locale('id').format('Do MMMM YYYY');


	const [tampil, setTampil] = React.useState(false);
	const handleKirim = () => {
		setTampil(true);
	};

	const handleCloseKirim = () => {
		setTampil(false);
	};

	return (
		<React.Fragment>
			<div className="row">
				<div className="col-xl-8">
					<div className="card bg-default">
						<div className="card-header">
			              <div className="row align-item-center">
								<div className="col">
									<h6 className="text-uppercase text-muted ls-1 mb-1">Rangkuman Materi Selama Perkuliahan</h6>
									<h5 className="h3 mb-0">MATERI PERKULIAHAN</h5>
								</div>
							</div>
			            </div>

			        {/* start map */}
			            { dataArsip && dataArsip.data.map((value, index) => <div key={index} className="card-header d-flex align-items-center">

			            

			              <div className="d-flex align-items-center">
			                
			                <div className="mx-3">
			                  <span className="text-dark font-weight-600 text-sm">{ value.keterangan }</span>
			                  <small className="d-block text-muted">{ moment(value.dated_created).locale('id').format('Do MMMM YYYY') }</small>
			                </div>
			              </div>
			              <div className="text-right ml-auto">
			              	<a href={`${variabelConstant.alamat}${value.file_pelajaran}`} download>
			                <button type="button" className="btn btn-sm btn-primary btn-icon">
			                  <span className="btn-inner--icon"><i className="ni ni-fat-add"></i></span>
			                 
			                  <span className="btn-inner--text">Download</span>
			                </button>
			                </a>
			              </div>
			            </div>

			            )}

						{/* end map */}

					</div>
				</div> 
			{/* end col 8 */}

			<div className="col-xl-4">
					<div className="card">
						<div className="card-header">
							<h5 className="h3 mb-0">Tugas</h5>
						</div>
						<div className="card-body">
							{/* ========================= ul ==================================*/}
							<ul className="list-group list-group-flush list my--3">
				                <li className="list-group-item px-0">
				                  <div className="row align-items-center">
				                    <div className="col-auto">
				                      {/* Avatar */}
				                      <span className="avatar rounded-circle">
				                      </span>
				                    </div>
				                    <div className="col ml--2">
				                      <h4 className="mb-0">
				                        <span>Belum Ada Tugas</span>
				                      </h4>
				                      <span className="text-success">●</span>
				                      <small>-</small>
				                    </div>
				                    <div className="col-auto">
				                      <button disabled type="button" onClick={handleKirim} className="btn btn-sm btn-primary">
				                      Kirim
				                      </button>
				                    
				                    {/* modal kirim*/}
				                    	{ tampil && 
				                    	<Modal show={tampil} onHide={handleCloseKirim}>
				                    		<Modal.Header>
				                    			<h5 className="h3 mb-0">Tugas</h5>
				                    		</Modal.Header>

				                    		<Modal.Body>
				                    			<form>
				                    				<div className="form-group row">
									                    <label htmlFor="example-text-input" className="col-md-2 col-form-label form-control-label">Nama</label>
									                    <div className="col-md-10">
									                      <input className="form-control" type="text" id="example-text-input" />
									                    </div>
									                  </div>
									                 <div className="form-group row">
									                    <label htmlFor="example-text-input" className="col-md-2 col-form-label form-control-label">NIM</label>
									                    <div className="col-md-10">
									                      <input className="form-control" type="text" id="example-text-input" />
									                    </div>
									                  </div>
									                  <div className="form-group row">
									                    <label htmlFor="example-text-input" className="col-md-2 col-form-label form-control-label">Ket.</label>
									                    <div className="col-md-10">
									                      <input className="form-control" placeholder="misal Tugas 1" type="text" id="example-text-input" />
									                    </div>
									                  </div>
									                  <div className="form-group row">
									                    <label htmlFor="example-text-input" className="col-md-2 col-form-label form-control-label">File Tugas</label>
									                    <div className="col-md-10">
									                  		<input type="file"  />
									                    </div>
									                  </div>
				                    			</form>
				                    		</Modal.Body>
				                    		<Modal.Footer>
				                    			<button className="btn btn-primary" onClick={handleCloseKirim}>Kirim</button>
				                    			<button className="btn btn-secondary" onClick={handleCloseKirim}>Batal</button>
				                    		</Modal.Footer>

				                    	</Modal>}
				                	{/* end modal kirim*/}
				                    </div>
				                  </div>
				                </li>
				                
				              </ul>
							{/* ========================= end ul ==================================*/}
						</div>
					</div>
				</div> 
			{/* end col 4 */}
			</div>

			
		</React.Fragment>
	)
};

export default Content;