import http from '../component/alamatApi';
import qs from 'qs';

class Db {
	getAll() {
		return http.get('/apriori/hitung_prediksi');
	};

	// hasil apriori belum ada paginasinya (data keseluruhan)
	getAllApriori() {
		return http.get('/apriori/hitung_apriori');
	};

	postApriori(inputan) {
		return http.post('/apriori/hitung_apriori', inputan);
	}

	inputanApriori(mydata) {
		return http.post('/apriori/inputan_apriori', qs.stringify(mydata), {
			headers: {
					"content-type": "application/x-www-form-urlencoded"
				}
		});
	}

	pencarianPost(mydata) {
		return http.post('/apriori/cari', qs.stringify(mydata), {
			headers: {
				"content-type": "application/x-www-form-urlencoded"
			}
		})
	}

	// yang ini hasil apriori sudah ada paginasinya
	listApriori() {
		return http.get('/apriori/hasil_apriori');
	}

	// url yang ada di halaman urls.py
	mydataMyapi(id) {
		return http.get('/myapi/', {
			params: {
				page: id
			}
		});
	};

	detailDataMyapi(idnya) {
		return http.get(`/myapi/detail_data/${idnya}`);
	};

	removeDataMyapi(mydata) {
		return http.post('/myapi/remove_data', qs.stringify(mydata, { arrayFormat: 'repeat' }), {
			headers: {
				"content-type": "application/x-www-form-urlencoded"
			}
		})
	}

	createData(mydata) {
		return http.post('/myapi/create_data', qs.stringify(mydata), {
			headers: {
				"content-type": "application/x-www-form-urlencoded"
			}
		})
	}

	updateDataProfil(mydata, idnya) {
		return http.post(`/myapi/update_data_profile/${idnya}`, qs.stringify(mydata), {
			headers: {
				"content-type": "application/x-www-form-urlencoded"
			}
		})
	}

	updateDataAnggotaMyapi(mydata, idnya) {
		return http.post(`/myapi/update_data_anggota/${idnya}`, qs.stringify(mydata), {
			headers: {
				"content-type": "application/x-www-form-urlencoded"
			}
		})
	}

	cariData(mydata) {
		return http.post('/myapi/cari_data', qs.stringify(mydata), {
			headers: {
				"content-type": "application/x-www-form-urlencoded"
			}
		})
	}

	// data untuk materi

	listMateri() {
		return http.get('/myapi/materi');
	}

	listArsip(id) {
		return http.get(`/myapi/arsip/${id}`);   // id: adalah idnya mapel (1,2,5)
	}

	// download materi
	downloadArsip(alamat) {
		return http.get(alamat);
	}

	dataAutentifikasi(id) {
		return http.get(`/myapi/autentifikasi`);  
	}
}

export default new Db();