import { actionTypes } from '../constants/action_types';

const initialState = {
	dataPrediksi: [],
	loading: false,
	dataApriori: [],
	inputanApriori: []
}

const initialDataPrediksi = {
	item: [],
	loading: false,
	error: null,
}

const initialDataAnggota = {
	item: [],
	loading: false,
	error: null,
}

const initialDetailAnggota = {
	itemdetail: [],
	loading: false,
	error: null,
}

const initialDetailArsip = {
	itemdetail: {
		data: [],
	},
	loading: false,
	error: null,
}

const initialAutentifikasi = {
	itemdetail: {},
	loading: false,
	error: null,
}

export const dataku = (state=0, action) => {
	switch(action.type) {
		case actionTypes.SET_DATA_PREDIKSI:
			return state + 1;
		default:
			return state
	}
};

export const dataPrediksi = (state=initialState, action) => {
	switch(action.type) {
		case 'SET_DATA_PREDIKSI':
			return {...state, dataPrediksi: action.payload}
		case actionTypes.ISLOADING:
			return {...state, loading: action.payload }
		case actionTypes.SET_DATA_APRIORI:
			return {...state, dataApriori: action.payload}
		case actionTypes.SET_DATA_INPUTAN_APRIORI:
			return {...state, inputanApriori: action.payload}
		default:
			return state
	}
};

export const naikanAngka = (state=0, action) => {
	console.log('oke jalan naikan angka');
	switch(action.type) {
		case actionTypes.NAIKAN:
			return state + 1;
		default:
			return state;
	}
};

export const mydata = (state=initialDataPrediksi, action) => {
	switch(action.type) {
		case actionTypes.PROSES_AMBIL_DATA:
			return {
				...state,
				item: [],
				loading: true,
				error: null,
			}
		case actionTypes.FETCH_DATA_PREDIKSI:
			return {
				...state,
				item: action.payload,
				loading: false,
				error: null,
			}
		case actionTypes.GAGAL_AKSES:
			return {
				...state,
				item: [],
				loading: false,
				error: 'ada error diserver'
			}
		default:
			return state;
	}
}

