import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import Cookies from 'js-cookie';
import $ from 'jquery';

import { Scrollbars } from 'react-custom-scrollbars';

const Nav = () => {
  const location = useLocation();
  // const [isToggleOn, setIsToggleOn] = React.useState(false);
  const [isToggleOnData, setIsToggleOnData] = React.useState(false);

  // const handleToggle = (e) => {
  //   e.preventDefault()
  //   setIsToggleOn(prev => !prev);

  // };

  const handleToggleData = (e) => {
    e.preventDefault()
    setIsToggleOnData(prev => !prev);

  };

  React.useEffect(() => {

    //inisialisasi
    


    // pined aside bar
    var Layout = (function() {

          function pinSidenav() {
              $('.sidenav-toggler').addClass('active');
              $('.sidenav-toggler').data('action', 'sidenav-unpin');
              $('body').removeClass('g-sidenav-hidden').addClass('g-sidenav-show g-sidenav-pinned');
              $('body').append('<div class="backdrop d-xl-none" data-action="sidenav-unpin" data-target='+$('#sidenav-main').data('target')+' />');

              // Store the sidenav state in a cookie session
              Cookies.set('sidenav-state', 'pinned');
          }

          function unpinSidenav() {
              $('.sidenav-toggler').removeClass('active');
              $('.sidenav-toggler').data('action', 'sidenav-pin');
              $('body').removeClass('g-sidenav-pinned').addClass('g-sidenav-hidden');
              $('body').find('.backdrop').remove();

              // Store the sidenav state in a cookie session
              Cookies.set('sidenav-state', 'unpinned');
          }

          // Set sidenav state from cookie

          var $sidenavState = Cookies.get('sidenav-state') ? Cookies.get('sidenav-state') : 'pinned';

          if($(window).width() > 1200) {
              if($sidenavState === 'pinned') {
                  pinSidenav()
              }

              if(Cookies.get('sidenav-state') === 'unpinned') {
                  unpinSidenav()
              }
          }

          $("body").on("click", "[data-action]", function(e) {

              e.preventDefault();

              var $this = $(this);
              var action = $this.data('action');
              var target = $this.data('target');


              // Manage actions

              switch (action) {
                  case 'sidenav-pin':
                      pinSidenav();
                  break;

                  case 'sidenav-unpin':
                      unpinSidenav();
                  break;

                  case 'search-show':
                      target = $this.data('target');
                      $('body').removeClass('g-navbar-search-show').addClass('g-navbar-search-showing');

                      setTimeout(function() {
                          $('body').removeClass('g-navbar-search-showing').addClass('g-navbar-search-show');
                      }, 150);

                      setTimeout(function() {
                          $('body').addClass('g-navbar-search-shown');
                      }, 300)
                  break;

                  case 'search-close':
                      target = $this.data('target');
                      $('body').removeClass('g-navbar-search-shown');

                      setTimeout(function() {
                          $('body').removeClass('g-navbar-search-show').addClass('g-navbar-search-hiding');
                      }, 150);

                      setTimeout(function() {
                          $('body').removeClass('g-navbar-search-hiding').addClass('g-navbar-search-hidden');
                      }, 300);

                      setTimeout(function() {
                          $('body').removeClass('g-navbar-search-hidden');
                      }, 500);
                  break;
              }
          })


          // Add sidenav modifier classes on mouse events

          $('.sidenav').on('mouseenter', function() {
              if(! $('body').hasClass('g-sidenav-pinned')) {
                  $('body').removeClass('g-sidenav-hide').removeClass('g-sidenav-hidden').addClass('g-sidenav-show');
              }
          })

          $('.sidenav').on('mouseleave', function() {
              if(! $('body').hasClass('g-sidenav-pinned')) {
                  $('body').removeClass('g-sidenav-show').addClass('g-sidenav-hide');

                  setTimeout(function() {
                      $('body').removeClass('g-sidenav-hide').addClass('g-sidenav-hidden');
                  }, 300);
              }
          })


          // Make the body full screen size if it has not enough content inside
          $(window).on('load resize', function() {
              if($('body').height() < 800) {
                  $('body').css('min-height', '100vh');
                  $('#footer-main').addClass('footer-auto-bottom')
              }
          })

      })();

  }, []);
 
	return (
		<React.Fragment>
		 <nav className="sidenav navbar navbar-vertical fixed-left navbar-expand-xs navbar-light bg-white" id="sidenav-main">
    <Scrollbars autoHide>
    <div className="scrollbar-inner">
      <div className="sidenav-header d-flex align-items-center">
        <a className="navbar-brand" href="/#" rel="noopener noreferrer">
          <h2>ISTI</h2>
        </a>
        <div className="ml-auto">
          <div className="sidenav-toggler d-none d-xl-block" data-action="sidenav-unpin" data-target="#sidenav-main">
            <div className="sidenav-toggler-inner">
              <i className="sidenav-toggler-line"></i>
              <i className="sidenav-toggler-line"></i>
              <i className="sidenav-toggler-line"></i>
            </div>
          </div>
        </div>
      </div>
      <div className="navbar-inner">
        <div className="collapse navbar-collapse" id="sidenav-collapse-main">
          <ul className="navbar-nav">
          <li className="nav-item">
              <Link
                className={location.pathname === '/' ? "nav-link active" : "nav-link"}
                to="/">
                <i className="ni ni-shop text-primary"></i>
                <span className="nav-link-text">Dashboard</span>
              </Link>
            </li>
            
             
            <li className="nav-item">
              <a
                onClick={handleToggleData}
                className={location.pathname === '/kb' || location.pathname === '/web' || location.pathname === '/md' ? "nav-link active" : "nav-link collapsed"}
                data-toggle="collapse"
                role="button"
                aria-expanded={isToggleOnData ? "true": "false"}
                rel="noopener noreferrer" href="/kb"
                aria-controls="navbar-examples">
                <i className="ni ni-ungroup text-orange"></i>
                <span className="nav-link-text">Mata Kuliah</span>
              </a>
              <div className={isToggleOnData ? "collapse show" : "collapse"} id="navbar-examples">
                <ul className="nav nav-sm flex-column">
                  
                 <li className="nav-item">
                    <Link rel="noopener noreferrer" to="/kb" className="nav-link">Kecerdasan Buatan</Link>
                  </li>
                  <li className="nav-item">
                    <Link rel="noopener noreferrer" to="/web" className="nav-link">WEB</Link>
                  </li>
                  <li className="nav-item">
                    <Link rel="noopener noreferrer" to="/md" className="nav-link">Matematika Diskrit</Link>
                  </li>
                  

                </ul>
              </div>
            </li>
           
          </ul>
          
        </div>
      </div>
    </div>
    </Scrollbars>
  </nav>

		</React.Fragment>
	)
};

export default Nav;