import React from 'react';
import Content from './Content';
import ContentSkeleton from './ContentSkeleton';
import Footer from './Footer';

import { useDispatch, useSelector } from 'react-redux';
import { listDataArsip } from '../redux/actions/myactions';

const KecerdasanBuatan = () => {
	const dispatch = useDispatch();
	const loading = useSelector(state => state.semuaReducers.detailArsip.loading);

	React.useEffect(() => {
		dispatch(listDataArsip(2));  // id 2 adalah id dari mapel
	}, [dispatch]);

	return (
		<React.Fragment>
			<div className="container-fluid mt--6">
			{ loading ? <ContentSkeleton /> : <Content /> }
			<Footer />
			</div>

		</React.Fragment>
	)
};

export default KecerdasanBuatan;