const initialState = {
	mydata: [],
	loading: false,
}

export const allDataReducer = (state=initialState, action) => {
	switch(action.type) {
		case 'SET_DATA':
			return {...state, mydata: action.payload }
		case 'SET_LOADING_TRUE':
			return {...state, loading: true}
		case 'SET_LOADING_FALSE':
			return {...state, loading: false }
		default:
			return state
	}
};
